import React, { useState } from "react";
import Moralis from "moralis/dist/moralis.min.js";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import { Spinner } from "react-bootstrap";

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #212428;
  }
`;

const Register = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [registration, setRegitration] = useState(false);

  const handleEmailChange = (event) => setEmail(event.target.value);
  const handlePasswordChange = (event) => setPassword(event.target.value);
  const handleUsernameChange = (event) => setUsername(event.target.value);

  const signupFunc = async (e) => {
    console.log(username, password, email);

    e.preventDefault();
    setRegitration(true);
    const user = new Moralis.User();
    user.set("username", username);
    user.set("password", password);
    user.set("email", email);

    try {
      await user.signUp();
      alert("succesfully Signed up");
      // Hooray! Let them use the app now.
    } catch (error) {
      // Show the error message somewhere and let the user try again.
      alert("Error: " + error.code + " " + error.message);
    }
    // login(username, password, email);
    setRegitration(false);
  };

  return (
    <div>
      <GlobalStyles />

      <section className="container">
        <div className="row">
          <div className="spacer-double"></div>
          <div className="col-md-8 offset-md-2">
            <h3>Don't have an account? Register now.</h3>
            <p>
              Sed ut perspiciatis unde omnis iste natus error sit voluptatem
              accusantium doloremque lasdasd asd as d asd as d as das d asd
              asudantium, totam rem aperiam, eaque ipsa quae ab illo inventore
              veritatis et quasi architecto beatae vitae dicta sunt explicabo.
            </p>

            <div className="spacer-10"></div>

            <form
              name="contactForm"
              id="contact_form"
              className="form-border"
              action="#"
            >
              <div className="row">
                {/* <div className="col-md-6">
                  <div className="field-set">
                    <label>Name:</label>
                    <input
                      type="text"
                      name="name"
                      id="name"
                      className="form-control"
                    />
                  </div>
                </div> */}

                <div className="col-md-6">
                  <div className="field-set">
                    <label>Email Address:</label>
                    <input
                      value={email}
                      onChange={handleEmailChange}
                      type="text"
                      name="email"
                      id="email"
                      className="form-control"
                    />
                  </div>
                </div>

                <div className="col-md-6">
                  <div className="field-set">
                    <label>Choose a Username:</label>
                    <input
                      value={username}
                      onChange={handleUsernameChange}
                      type="text"
                      name="username"
                      id="username"
                      className="form-control"
                    />
                  </div>
                </div>

                {/* <div className="col-md-6">
                  <div className="field-set">
                    <label>Phone:</label>
                    <input
                      type="text"
                      name="phone"
                      id="phone"
                      className="form-control"
                    />
                  </div>
                </div> */}

                <div className="col-md-6">
                  <div className="field-set">
                    <label>Password:</label>
                    <input
                      value={password}
                      onChange={handlePasswordChange}
                      type="password"
                      name="password"
                      id="password"
                      className="form-control"
                    />
                  </div>
                </div>

                {/* <div className="col-md-6">
                  <div className="field-set">
                    <label>Re-enter Password:</label>
                    <input
                      type="text"
                      name="re-password"
                      id="re-password"
                      className="form-control"
                    />
                  </div>
                </div> */}

                <div className="col-md-12">
                  <div id="submit" className="pull-left">
                    <button
                      onClick={signupFunc}
                      type="submit"
                      id="send_message"
                      className="btn btn-main color-2"
                    >
                      Register Now
                    </button>
                  </div>

                  <div className="clearfix"></div>
                  {registration ? (
                    <div className="d-flex justify-content-center">
                      <Spinner
                        animation="border"
                        variant="danger"
                        style={{ width: "100px", height: "100px" }}
                      />
                    </div>
                  ) : null}
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
};

export default Register;
