import React, { useEffect, useState } from "react";
import Select from "react-select";
import ColumnNew from "../components/ColumnNew";
import Footer from "../components/footer";
import { useQuery } from "react-query";
import NFtService from "../../services/NFtService";
import useDebounce from "../../hooks/useDebounce";
import NftCarosel from "../../components/components/NftCarosel";
import { SpinnerDotted } from "spinners-react";

const customStyles = {
  option: (base, state) => ({
    ...base,
    background: "#212428",
    color: "#fff",
    borderRadius: state.isFocused ? "0" : 0,
    "&:hover": {
      background: "#16181b",
    },
  }),

  menu: (base) => ({
    ...base,
    background: "#212428 !important",
    borderRadius: 0,
    marginTop: 0,
  }),
  menuList: (base) => ({
    ...base,
    padding: 0,
  }),
  control: (base, state) => ({
    ...base,
    padding: 2,
  }),
};
const options = [
  { value: "bsc", label: "Global 🌎" },
  { value: "eth", label: "Eth" },
];

const limit = 12;

const Explore = () => {
  const [query, setQuery] = useState("rare");

  const [nftData, setNFTData] = useState([]);

  const [page, setPage] = useState(0);

  const val = useDebounce(query, 300);
  const [chainType, setChainType] = useState(options[0]);

  const { isLoading, data } = useQuery(
    `search-nfts-${val}-${page}-${chainType.value}`,
    () => {
      if (!val) {
        return;
      }
      return NFtService.searchNftData(chainType.value, val, {
        limit: limit,
        offset: page * limit,
      });
    },
    { staleTime: 60 * 1000 }
  );

  useEffect(() => {
    setNFTData([]);
  }, [val]);

  useEffect(() => {
    setNFTData([]);
  }, [chainType]);

  useEffect(() => {
    if (!data) {
      return;
    }
    setNFTData((prev) => [...prev, ...(data?.result || [])]);
  }, [data]);

  console.log(`search-nfts-${val}-${page}-${chainType.value}`, chainType);

  const handleLoadMore = () => {
    setPage(page + 1);
  };

  return (
    <div>
      <section className="jumbotron breadcumb no-bg">
        <div className="mainbreadcumb">
          <div className="container">
            <div className="row m-10-hor">
              <div className="col-12">
                <h1 className="text-center">Explore NFTs</h1>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container">
        <div className="row">
          <div className="col-lg-12">
            <div className="items_filter">
              <form
                className="row form-dark explore_quick_search"
                id="form_quick_search"
                name="form_quick_search"
                onSubmit={(e) => {
                  e.preventDefault();
                }}
              >
                <div className="col">
                  <input
                    className="form-control"
                    id="name_1"
                    name="name_1"
                    placeholder="search item here..."
                    type="text"
                    value={query}
                    onChange={(e) => setQuery(e.target.value)}
                  />
                  <button id="btn-submit">
                    <i className="fa fa-search bg-color-secondary"></i>
                  </button>
                  <div className="clearfix"></div>
                </div>
              </form>

              <div className="dropdownSelect three ">
                <h4>Chain Type</h4>
                <Select
                  className="select1"
                  styles={customStyles}
                  // defaultValue={options[0]}
                  onChange={(value) => {
                    setChainType(value);
                  }}
                  options={options}
                  value={chainType}
                />
              </div>
            </div>
          </div>
        </div>

        <div>
          <NftCarosel data={nftData} />
          {nftData.length <= data?.total && (
            <button onClick={handleLoadMore} className="btn-main lead m-auto">
              Load-more
            </button>
          )}
          {isLoading && (
            <div className="w-100 d-flex justify-content-center">
              <SpinnerDotted enabled={true} color={"red"} />
            </div>
          )}
        </div>
        {/* {nftData.length === 0 && <ColumnNew />} */}
      </section>

      <Footer />
    </div>
  );
};
export default Explore;
