import React, { useEffect, useState } from "react";
import Moralis from "moralis/dist/moralis.min.js";
import { create } from "ipfs-http-client";
import { Spinner } from "react-bootstrap";

import Clock from "../components/Clock";
import Footer from "../components/footer";
const ipfsClient = require('ipfs-http-client');
const projectId = '2BeS6X13R250uEZ6Iq5zDtSrCA9';
const projectSecret = '864d1dfddf4362bc593458ac82856479';
const auth =
    'Basic ' + Buffer.from(projectId + ':' + projectSecret).toString('base64');

const client = ipfsClient.create({
    host: 'ipfs.infura.io',
    port: 5001,
    protocol: 'https',
    headers: {
        authorization: auth,
    },
});


let res;

function Createpage() {
  const [title, setTitle] = useState();
  const [description, setDescription] = useState();
  const [price, setPrice] = useState();
  const [ipfsLink, setipfsLink] = useState("your ipfs link here");
  const [royalties, setRoyalties] = useState();
  const [fileUrl, updateFileUrl] = useState(``);
  const [viewNFT, setViewNFT] = useState(false);
  const [creatingNFT, setCreatingNFT] = useState(false);

  async function onChangeFile(e) {
    const file = e.target.files[0];
    try {
      const added = await client.add(file);
      const url = `https://ipfs.infura.io/ipfs/${added.path}`;
      updateFileUrl(url);
      setipfsLink(`your file is uploaded to : ${url}`)
    } catch (error) {
      console.log("Error uploading file: ", error);
    }
  }

  const handleTitle = (e) => {
    setTitle(e.target.value);
  };

  const handleDescription = (e) => {
    setDescription(e.target.value);
  };

  const handlePrice = (e) => {
    setPrice(e.target.value);
  };

  const handleRoyalties = (e) => {
    setRoyalties(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const web3 = await Moralis.enableWeb3();
    const currentUser = Moralis.User.current();

    setCreatingNFT(true);

    let metadata = {
      name: title,
      description: description,
      price: price,
      image: "/ipfs/" + fileUrl,
    };

    const jsonfile = new Moralis.File("metadata.json", {
      base64: btoa(JSON.stringify(metadata)),
    });

    await jsonfile.saveIPFS();
    let metadataHash = jsonfile.hash();
    let address = currentUser.get("ethAddress");

    res = await Moralis.Plugins.rarible.lazyMint({
      chain: "rinkeby",
      // before deploy  change chain to mainnet
      // chain: 'miannet'
      // userAddress: address,
      userAddress: "0x9A0Cc755359A073E54E2531ACFCD3C2977Dd86DB",
      tokenType: "ERC721",
      tokenUri: "/ipfs/" + metadataHash,
      supply: 100,
      royaltiesAmount: 5,
    });
    setCreatingNFT(false);
    setViewNFT(true);
  };
  const copyToClipboard = e => {
    navigator.clipboard.writeText(window.location.toString())
  }
  const ViewNFT = () => {
    let token_address = res.data.result.tokenAddress;
    let token_id = res.data.result.tokenId;
    // before deploy change the address of url
    // let url=`https://rarible.com/token/${token_address}:${token_id}`
    let url = `https://rinkeby.rarible.com/token/${token_address}:${token_id}`;
    window.open(url, "_blank");
  };
  return (
    <div>
      <section className="jumbotron breadcumb no-bg">
        <div className="mainbreadcumb">
          <div className="container">
            <div className="row m-10-hor">
              <div className="col-12">
                <div className="d-flex justify-content-center align-items-center flex-column">
                  {viewNFT ? (
                    <>
                      <button onClick={ViewNFT} className="btn-main">
                        View Recently Created NFT
                      </button>
                      <button
                        onClick={() =>
                          window.open(
                            `https://rinkeby.rarible.com/user/0x9a0cc755359a073e54e2531acfcd3c2977dd86db/owned`,
                            "_blank"
                          )
                        }
                        className="btn-main bg-warning mt-4"
                      >
                        View All Owned NFTs
                      </button>
                    </>
                  ) : (
                    <h1>Create NFT</h1>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className={viewNFT ? "d-none" : "container"}>
        <div className="row">
          <div className="col-lg-7 offset-lg-1 mb-5">
            <form id="form-create-item" className="form-border">
              <div className="field-set">
                <h5>Upload file</h5>
                <div className="d-create-file">
                  <p id="file_name">PNG, JPG, GIF, WEBP or MP4. Max 200mb.</p>
                  {/* {this.state.files.map(x => 
                          <p key="{index}">PNG, JPG, GIF, WEBP or MP4. Max 200mb.{x.name}</p>
                          )} */}
                  <div className="browse">
                    <input
                      type="button"
                      id="get_file"
                      className="btn-main"
                      value="Browse"
                    />
                    {/* <input id='upload_file' type="file" multiple  onChange={handleImage}/> */}
                    <input
                      id="upload_file"
                      type="file"
                      onChange={onChangeFile}
                    />
                  </div>
                  <h1>IPFS Example</h1>
                </div>
                <div className="spacer-single"></div>
                <div className="spacer-single"><h6 className="ipfs-url">{ipfsLink}</h6></div><br/>
                <h5>Title</h5>
                <input
                  type="text"
                  name="item_title"
                  id="item_title"
                  className="form-control"
                  placeholder="e.g. 'Crypto Funk"
                  onChange={handleTitle}
                />
                <div className="spacer-10"></div>
                <h5>Description</h5>
                <textarea
                  data-autoresize
                  name="item_desc"
                  id="item_desc"
                  className="form-control"
                  placeholder="e.g. 'This is very limited item'"
                  onChange={handleDescription}
                ></textarea>
                <div className="spacer-10"></div>
                <h5>Price</h5>
                <input
                  type="number"
                  name="item_price"
                  id="item_price"
                  className="form-control"
                  placeholder="enter price for one item (ETH)"
                  onChange={handlePrice}
                />
                <div className="spacer-10"></div>
                <h5>Royalties</h5>
                <input
                  type="text"
                  name="item_royalties"
                  id="item_royalties"
                  className="form-control"
                  placeholder="suggested: 0, 10%, 20%, 30%. Maximum is 70%"
                  onChange={handleRoyalties}
                />
                <div className="spacer-10"></div>
                <button
                  type="submit"
                  id="submit"
                  className="btn-main"
                  onClick={handleSubmit}
                >
                  {" "}
                  Create Item
                </button>
              </div>
            </form>
            {creatingNFT ? (
              <div className="d-flex justify-content-center">
                <Spinner
                  animation="border"
                  variant="danger"
                  style={{ width: "100px", height: "100px" }}
                />
              </div>
            ) : null}
          </div>

          <div className="col-lg-3 col-sm-6 col-xs-12">
            <h5>Preview item</h5>
            <div className="nft__item m-0">
              <div className="de_countdown">
                <Clock deadline="December, 30, 2021" />
              </div>
              <div className="author_list_pp">
                <span>
                  <img
                    className="lazy"
                    src="./img/author/author-1.jpg"
                    alt=""
                  />
                  <i className="fa fa-check"></i>
                </span>
              </div>
              <div className="nft__item_wrap">
                <span>
                  <img
                    src={
                      fileUrl ? fileUrl : "./img/collections/coll-item-3.jpg"
                    }
                    id="get_file_2"
                    className="lazy nft__item_preview"
                    alt=""
                  />
                </span>
              </div>
              <div className="nft__item_info">
                <span>
                  <h4>{title || "Title"}</h4>
                </span>
                <div className="nft__item_price">
                  {description || "Description"}
                </div>
                <div className="nft__item_price">
                  <span>{price}</span> ETH
                </div>
                <div className="nft__item_action">
                  <span>Place a bid</span>
                </div>
                <div className="nft__item_like">
                  <i className="fa fa-heart"></i>
                  <span>50</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
}

export default Createpage;
